header {
  color: #fff;
  background-color: #3498db;
  -ms-flex-pack: center;
  justify-content: center;
  -ms-flex-align: center;
  align-items: center;
  padding: 10px;
  display: -ms-flexbox;
  display: flex;
}

header h1 {
  font-size: 24px;
}

@media screen and (max-width: 768px) {
  header h1 {
    font-size: 19.2px;
  }
}

footer {
  color: #fff;
  text-align: center;
  background-color: #3498db;
  padding: 10px;
}

body {
  background-color: #ecf0f1;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

nav {
  background-color: #2ecc71;
}

nav ul {
  background-color: #2ecc71;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: -ms-flexbox;
  display: flex;
}

nav ul li a {
  color: #fff;
  padding: 10px;
  text-decoration: none;
}

nav ul li a.active {
  border-bottom: 2px solid #3498db;
}

main {
  padding: 20px;
}

main section#evaluation {
  color: #fff;
  background-color: #34495e;
  margin-top: 20px;
  padding: 20px;
}

